@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap%27);
*{  
    font-family: 'Open Sans', sans-serif;
    padding: 0;
    margin: 0;
    color: #c0c0c0;
    box-sizing: border-box;
}

div, header, main {
    width: 100%;
}

html, header {
    background: linear-gradient(to right, #14213D, #000000);
}

html {
    scroll-behavior: smooth;
    font-size: 11px;
}

@media (min-width: 700px) {
    html {
        font-size: 13px;
    }
}

@media (min-width: 1280px) {
    html {
        font-size: 16px;
    }
}

@media (min-width: 1600px) {
    html {
        font-size: 20px;
    }
}

h1 {
    font-size: 1.7rem;
}

h2 {
    font-size: 1.3rem;
}

h3 {
    font-size: 1.1rem; 
}

p, a, li, ul {
    font-size: 1rem; 
}

main {
    padding: 20px;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
    min-height: 100vh;
    
}

hr {
    width: 100%
}

a {
    cursor: pointer;
}
::-webkit-scrollbar {
    width: 7px;
    background: black;
    /* mexe na scroll como um todo*/
}

::-webkit-scrollbar-thumb {
    background: skyblue;
    border-radius: 15px;
    /* mexe com a parte movel da scroll*/
}


